<template>
  <main>
    <input type="text" v-model="curentnumber" />
    <div class="keys">
      <button class="op__key" @click="clearitem()">C</button>
      <button class="op__key" @click="pluseminusnumber()">+/-</button>
      <button class="op__key" @click="operationitem('%')">%</button>
      <button class="op__key" @click="operationitem('/')">/</button>
      <button class="num__key" @click="addnumber('7')">7</button>
      <button class="num__key" @click="addnumber('8')">8</button>
      <button class="num__key" @click="addnumber('9')">9</button>
      <button class="op__key" @click="operationitem('*')">x</button>
      <button class="num__key" @click="addnumber('4')">4</button>
      <button class="num__key" @click="addnumber('5')">5</button>
      <button class="num__key" @click="addnumber('6')">6</button>
      <button class="op__key" @click="operationitem('-')">-</button>
      <button class="num__key" @click="addnumber('1')">1</button>
      <button class="num__key" @click="addnumber('2')">2</button>
      <button class="num__key" @click="addnumber('3')">3</button>
      <button class="op__key" @click="operationitem('+')">+</button>
      <span></span>
      <button class="num__key" @click="addnumber('0')">0</button>
      <button class="num__key" @click="adddot()">.</button>
      <button class="eq__key" @click="equal()">=</button>
    </div>
  </main>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      curentnumber: "",
      prevnumber: "",
      dataoperation: "",
    };
  },
  methods: {
    addnumber(num) {
      if (this.curentnumber == 0 && num == 0) {
        return;
      } else {
        this.curentnumber += num;
      }
    },
    adddot() {
      if (this.curentnumber.includes(".")) return;
      this.curentnumber += ".";
    },
    operationitem(operation) {
      console.log(operation);
      this.prevnumber = this.curentnumber;
      this.curentnumber = "";
      this.dataoperation = operation;
    },

    clearitem() {
      this.curentnumber = "";
    },
    pluseminusnumber() {
      if (parseFloat(this.curentnumber) > 0) {
        this.curentnumber = "-" + this.curentnumber;
      } else {
        this.curentnumber = this.curentnumber.substring(1);
      }
    },
    equal() {
      switch (this.dataoperation) {
        case "+":
          this.curentnumber =
            parseFloat(this.prevnumber) + parseFloat(this.curentnumber);
          break;
        case "-":
          this.curentnumber =
            parseFloat(this.prevnumber) - parseFloat(this.curentnumber);
          break;
        case "/":
          this.curentnumber =
            parseFloat(this.prevnumber) / parseFloat(this.curentnumber);
          break;
        case "*":
          this.curentnumber =
            parseFloat(this.prevnumber) * parseFloat(this.curentnumber);
          break;
        case "%":
          this.curentnumber =
            parseFloat(this.prevnumber) % parseFloat(this.curentnumber);
          break;
      }
      this.curentnumber = this.curentnumber.toString();
    },
  },
};
</script>

